
.slide-left-enter,
.slide-right-leave-active {
  opacity: 0;
  -webkit-transform: translate(50vw, 0);
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0.1;
  -webkit-transform: translate(-50vw, 0);
}
.app-view {
  z-index: 1;
  width: 100vw;
  height: 100vh;
  overflow: auto;
  position: absolute;
  transition: transform 0.3s ease;
  -webkit-overflow-scrolling: touch;
}
.app-view-hidden {
  overflow: hidden;
}
.header {
  width: 100%;
  height: 1.5rem;
  z-index: 9;
  padding-left: 5%;
  position: fixed;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.51) 95%);
}
.header .iconfont {
  color: #fff;
  font-size: 0.8rem;
  top: 20%;
  position: relative;
}
.aside {
  z-index: 11;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  position: fixed;
  visibility: hidden;
}
.aside::-webkit-scrollbar {
  display: none!important;
  width: 0!important;
  height: 0!important;
  -webkit-appearance: none;
  opacity: 0!important;
}
.aside ul {
  margin: 0;
  float: left;
  width: 60%;
  height: 100%;
  padding: 1.3rem 0.5rem 0.5rem;
  overflow: auto;
  background: rgba(91, 116, 146, 0.75);
  transform: translate(-100%, 0);
  transition: transform 0.3s ease;
  -webkit-overflow-scrolling: touch;
}
.aside ul::-webkit-scrollbar {
  display: none!important;
  width: 0!important;
  height: 0!important;
  -webkit-appearance: none;
  opacity: 0!important;
}
.aside li {
  cursor: pointer;
  font-size: 0.43rem;
  list-style: none;
  color: #fff;
  margin-top: 20px;
}
.aside li .iconfont {
  float: right;
  font-size: 0.6rem;
}
.aside li.chose {
  color: #FFD300;
}
.aside .cover {
  width: 100%;
  height: 100%;
  opacity: 0;
  display: none;
  background: rgba(172, 185, 201, 0.4);
  transition: opacity 0.3s ease;
}
.aside.open ul {
  transform: translate(0);
}
.aside.open .cover {
  opacity: 1;
}
.aside.docked {
  visibility: visible;
}
.aside.docked .cover {
  display: block;
}
.circle {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.8);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  right: 5%;
  bottom: 5vw;
  position: fixed;
  z-index: 10;
}
.circle i {
  top: 50%;
  left: 50%;
  font-size: 0.6rem;
  color: #ACB9C9;
  transform: translate(-50%, -50%);
  position: absolute;
}
@media screen and (min-width: 640px) {
.app-view {
    width: 640px;
    left: 50%;
    transform: translate(-50%, 0);
}
.aside ul {
    width: 350px;
}
}



