
.app-view .swiper-container {
  width: 100%;
}
.app-view .swiper-slide {
  height: 8rem;
  overflow: hidden;
  position: relative;
}
.app-view .swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 1rem;
  width: 95%;
  text-align: right;
}
.app-view .list:nth-child(2) {
  margin-bottom: 0;
  padding-top: 0;
}
.app-view .list:nth-child(2) .list-time {
  top: -0.8rem;
}
.swiper-slide div {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.4;
  position: absolute;
  background-color: #5B7492;
}
.swiper-slide img {
  top: 50%;
  position: relative;
  transform: translate(0, -50%);
}
.swiper-slide h3 {
  width: 70%;
  color: #fff;
  margin: 0;
  font-size: 0.5rem;
  line-height: 1rem;
  right: 5%;
  bottom: 2.6rem;
  text-align: right;
  position: absolute;
  text-shadow: 1px 1px 10px rgba(0, 0, 0, 0.5);
}
.swiper-slide h3:before {
  content: "";
  width: 3rem;
  bottom: -0.6rem;
  right: 0;
  display: block;
  position: absolute;
  border: 2px solid #FFD300;
}
.list {
  width: 90%;
  z-index: 1;
  position: relative;
  padding-top: 0.8rem;
  margin: .8rem auto 0;
}
.list-time {
  top: 0;
  margin: 0;
  color: #fff;
  padding: 0 1rem;
  font-size: 0.4rem;
  line-height: 0.8rem;
  letter-spacing: 0.1rem;
  border-radius: 0.4rem;
  text-align: center;
  background-color: #FFD300;
  transform: translate(0, -50%);
  position: absolute;
  box-shadow: 0 3px 10px 0 rgba(91, 115, 146, 0.15);
}
.list-con {
  cursor: pointer;
  display: flex;
  padding: 0.3rem;
  margin-bottom: 0.4rem;
  border-radius: 0.15rem;
  align-items: center;
  background-color: #fff;
  box-shadow: 0 3px 10px 0 rgba(91, 115, 146, 0.15);
}
.list-con img {
  width: 2rem;
  margin-right: 0.4rem;
}
.list-con p {
  color: #5B7492;
  font-size: 0.4rem;
  text-align: justify;
  margin: 0;
}
.loading {
  width: 25%;
  height: 0.4rem;
  margin: 25% auto 0;
  position: relative;
}
.loading span {
  display: block;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 50%;
  position: absolute;
  background: #5B7492;
  transform: translate(-50%, 0);
}
.loading .left {
  background: #FFD300;
  animation: lMove 2.5s ease infinite;
}
.loading .middle {
  left: 50%;
  animation: mMove 2.5s ease infinite;
}
.loading .right {
  left: 100%;
  background: #acb9c8;
  animation: rMove 2.5s ease infinite;
}
@keyframes lMove {
0% {
    left: 0;
}
25% {
    left: 50%;
    background: #FFD300;
}
50% {
    left: 100%;
    background: #5B7492;
}
75% {
    left: 50%;
    background: #acb9c8;
}
100% {
    left: 0;
}
}
@keyframes mMove {
25% {
    background: #5B7492;
}
50% {
    background: #FFD300;
}
75% {
    background: #5B7492;
}
}
@keyframes rMove {
0% {
    left: 100%;
}
25% {
    left: 50%;
}
50% {
    left: 0;
    background: #acb9c8;
}
75% {
    left: 50%;
    background: #FFD300;
}
100% {
    left: 100%;
}
}



